//script src
function getPathDir() {
    let scriptSrc = "";
    let nodeList = document.querySelectorAll("script");
    for(let i = 0; i< nodeList.length;i++){
        if(/Cesiums[^\/]*$/.test(nodeList.item(i).src)){
            scriptSrc = nodeList.item(i).src;
            break;
        }
    }

    return scriptSrc.match(/(.+)\/(Cesiums.js.*|Cesiums.min.js)/)[1];
}

